import { unregister } from './registerServiceWorker';
import React from 'react';

const Unregister = () => {
  unregister();
  setTimeout(() => {window.location.reload();},5000)
  return (
    <div style={{textAlign: 'center', margin: '30px auto', paddingTop: '10vh', fontSize: '1.6em'}}>
    Service Worker for hkarn.github.io is unregistered.<br />
    Routing for subdirectories not included in the main app should now function properly.<br />
    <a href="#" onClick={() => {window.location.reload()}}> Click here to reload content. </a>
    </div>
  )
};

export default Unregister;




